import React from 'react'
import './Avatar.css'
import {host} from "../../methods/config";
export default function Avatar({ style, img }) {
    return (
        <div className='avatar' style={style}>
            <img src={host+"/avatars/"+img} style={{width: '100%', height: '100%', objectFit: 'cover'}}/>
        </div>
    )
}